@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans', sans-serif;

  --color-1: #2A6F97;
  --color-2: #E5E5E5;
  --color-3: #E0FBFC;
  --color-4: #EE6C4D;
  --color-5: #293241;
}

.header {
    position: relative;
    width: 100%;
    background-color: var(--color-1);
    overflow: hidden;
}

.header-content {
    display: flex;
    flex-direction: row;
    color: var(--color-3);
    background-color: var(--color-1);
    margin-bottom: 150px;
    align-items: center;
    padding: 40px 0;
}

.first-column-header {
    width: 50%;
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
}

.first-column-header h1 {
    font-size: 50px;
    padding: 10px;
}

.first-column-header h2 {
    font-size: 28px;
    padding: 10px 20px;
}

.first-column-header span {
    color: var(--color-4);
}

.second-column-header {
    width: 50%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.second-column-header > img {
    border-radius: 50%;
}

.second-column-header > div {
    display: flex;
    margin-top: 10px;
}

.second-column-header > div > p {
    font-size: 20px;
    margin: 10px 0 0 15px;
}

.header .wave {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background: url(data:image/png;base64,);
    background-size: 1000px 100px;
}

.header .waves .wave1 {
    animation: animate 30s linear infinite;
    z-index: 1000;
    opacity: 1;
    animation-delay: 0s;
    bottom: 0;
}

.header .waves .wave2 {
    animation: animate2 15s linear infinite;
    z-index: 999;
    opacity: 0.5;
    animation-delay: -5s;
    bottom: 10px;
}

.header .waves .wave3 {
    animation: animate 30s linear infinite;
    z-index: 998;
    opacity: 0.2;
    animation-delay: -2s;
    bottom: 15px;
}

.header .waves .wave4 {
    animation: animate2 5s linear infinite;
    z-index: 997;
    opacity: 0.7;
    animation-delay: -5s;
    bottom: 20px;
}

@keyframes animate {
    0% {
        background-position-x: 0;
    }
    100% {
        background-position-x: 1000px;
    }
}

@keyframes animate2 {
    0% {
        background-position-x: 0;
    }
    100% {
        background-position-x: -1000px;
    }
}

@media screen and (max-width: 800px) {
    .header-content {
        padding-top: 0;
        flex-direction: column;
    }

    .first-column-header, .second-column-header {
        width: 100%;
    }

    .second-column-header {
        margin-top: 20px;
    }

    .first-column-header h1 {
        padding-bottom: 0;
        font-size: 30px;
    }

    .first-column-header h2 {
        padding-top: 0;
        font-size: 20px;
    }
}

.timeline-box {
    padding: 10px 40px;
    position: relative;
    width: calc(50% - 80px);
}

.timeline-box::after {
    content: '';
    position: absolute;
    top: 15px;
    right: -20px;
    width: 30px;
    height: 30px;
    background: var(--color-3);
    border-radius: 50%;
    border: 4px solid var(--color-4);
}

.left {
    left: 0;
}

.right {
    left: 50%;
}

.right::after {
    left: -19px;
}

.left::before {
    content: '';
    height: 0;
    width: 0;
    position: absolute;
    top: 22px;
    right: 30px;
    border: solid var(--color-3);
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent var(--color-3);
}

.right::before {
    content: '';
    height: 0;
    width: 0;
    position: absolute;
    top: 22px;
    left: 30px;
    border: solid var(--color-3);
    border-width: 10px 10px 10px 0;
    border-color: transparent var(--color-3) transparent transparent;
}

.timeline-content {
    padding: 20px 30px;
    background: var(--color-3);
    border-radius: 6px;
}

.timeline-content > span {
    font-size: 1.17em;
    font-weight: bold;
}

@media screen and (max-width: 800px) {
    .timeline-box {
        width: calc(100% - 100px);
        padding-left: 70px;
        padding-right: 25px;
    }

    .timeline-box::before {
        left: 60px;
        border-width: 10px 10px 10px 0;
        border-color: transparent var(--color-3) transparent transparent;
    }

    .left::after, .right::after {
        left: 12px;
        z-index: 1;
    }

    .right {
        left: 0;
    }
}

.timeline {
    padding: 20px 0;
    background: var(--color-5);
}

.timeline-container {
    max-width: 1200px;
    margin: 40px auto;
    position: relative;
}

.timeline-container::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 6px;
    background: var(--color-3);
    left: 50%;
    margin-left: -3px;
    border-radius: 5px;
}

@media screen and (max-width: 800px) {
    .timeline-container::before {
        left: 31px;
    }
}

.session-title {
    text-align: center;
    width: 50%;
    padding: 30px;
    margin: 0 auto 30px;
    border-bottom: 1px solid;
}

.session-title span {
    font-size: 30px;
    font-weight: bold;
    letter-spacing: 2px;
}

.skill-content {
    text-align: center;
    width: 60px;
    height: 100px;
    margin: 20px 50px 0 50px;
}

.skills-container {
    margin-bottom: 50px;
}

.skills {
    max-width: 700px;
    margin: 0 auto;
    text-align: center;
}

.skills p {
    font-size: 18px;
    margin: 50px 20px 20px 20px;
}

.skill-box {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 15px;
}

.contacts-container {
    text-align: center;
    padding: 20px 0;
}

.contacts-container h4 {
    font-weight: lighter;
}

.contacts-content {
    width: 100%;
    display: flex;
    justify-content: center;
}

.contacts-content a {
    text-decoration: none;
    color: #000000;
}

.contacts-content img {
    width: 60px;
    height: 60px;
    margin: 0 20px;
}

.projects-session {
    background: var(--color-2);
    padding-bottom: 20px;
}

.projects-session a {
    color: black;
}

.projects-session > p {
    text-align: center;
    font-size: 18px;
    padding: 0 20px;
}

.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.project {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 600px;
    margin: 0 20px 20px 20px;
}

.project-description {
    max-width: 350px;
    text-align: center;
}

.project-gif img {
    width: 100%;
}

